import {gsap} from 'gsap';

const onLoad = () =>{
  gsap.timeline().fromTo(".letter",
  {
    x: -100,
    opacity:0
  },
  {
    x: 0,
    opacity:1,
    stagger: 0.1,
    delay : 0.2
  }


  ).to(".letter",{
    margin: "0 1.5vw",
    delay: 0.5,
    duration:2,
    easeOut : 'power3'
  })
}



export default onLoad;
