import {gsap} from 'gsap';
import Particle from './Particle';
import Letter from './Letter';
import Page1 from './Page1';
import Page2 from './Page2';
import p1 from "./parallax1.jpg";
import p2 from "./parallax2.png";
import p3 from "./parallax3.png";
import DivShow from './DivShow'


const Home = () =>{



  const Goto = () =>{
  gsap.to(window,{
    duration:3,
    scrollTo : "#nextSection"
  })
}



  return(
  <>

   <div className="title">
    <h1 className="Poppins" id="title" > <Letter /> </h1>
    <p className="Poppins" id="subtitle" >Jeune docteur de l'INALCO / Membre associé au CRLAO</p>
    <div className="btsuite" onClick={Goto} ></div>
    </div>
    <Particle />
    <div className="images">
    
    <div className="image-wrapper"> <img src={p1} id="p1"  />  </div>
    <div className="image-wrapper"> <img src={p2} id="p2" />  </div>
    <div className="image-wrapper"> <img src={p3} id="p3" />  </div>
    </div>
    <div className="box b1" id="nextSection" > <Page1 />  </div>
    <div className="box" id="page2" > <Page2 /></div>
    <div className="footer"> 
    <div className="footerblock1"></div>
    <div className="footerblock2">

    <p> Adeline Chan / contact </p>
    <h2> adelinecyzhi@gmail.com </h2>
    </div>
    <div className="footerblock3"></div>
    </div>
    </>
  )
}

export default Home;
